<template>
  <div id="app">
    <div id="controls">
      <div
        style="float: right; z-index: 3; width: 8px; height: 8px; background-color: red;"
        @click="settings.controls = !settings.controls"
      >

      </div>
      <table v-show="settings.controls">
        <tr>
          <td>background:</td>
          <td>
            R
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.bg.r"
            >
          </td>
          <td>
            G
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.bg.g"
            >
          </td>
          <td>
            B
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.bg.b"
            >
          </td>
          <td>
            A
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.bg.a"
            >
          </td>
        </tr>
        <tr>
          <td>circles:</td>
          <td>
            R
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.dot.r"
            >
          </td>
          <td>
            G
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.dot.g"
            >
          </td>
          <td>
            B
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.dot.b"
            >
          </td>
          <td>
            A
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.dot.a"
            >
          </td>
        </tr>
        <tr>
          <td>&nbsp;</td>
          <td>
            MinSize
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.dot.minSize"
            >
          </td>
          <td>
            MaxSize
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.dot.maxSize"
            >
          </td>
          <td>&nbsp;</td>
          <td>&nbsp;</td>
        </tr>
        <tr>
          <td>other:</td>
          <td>
            cellWidth
            <input
              min="10"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.grid.cellWidth"
            >
          </td>
          <td>
            cellHeight
            <input
              min="10"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.grid.cellHeight"
            >
          </td>
          <td>
            FPS
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.framerate"
            >
          </td>
          <td>
            realFps: {{realFps}}
          </td>
        </tr>
      </table>
    </div>
    <div id="p5"></div>
    <!--<vue-p5-->
      <!--@setup="setup"-->
      <!--@draw="draw"-->
    <!--/>-->
  </div>
</template>

<script>
// import VueP5 from 'vue-p5'
import P5 from 'p5/lib/p5.min.js'

export default {
  name: 'app',

  components: {
    // VueP5,
    // P5
  },

  data () {
    return {
      settings: {
        controls: true,
        framerate: 20,
        bg: {
          r: 30,
          g: 0,
          b: 0,
          a: 255
        },
        grid: {
          cols: 0,
          rows: 0,
          width: 0,
          height: 0,
          cellWidth: 30,
          cellHeight: 30
        },
        dot: {
          r: 222,
          g: 53,
          b: 72,
          a: 255,
          maxSize: 10,
          minSize: 0
        }
      },
      data: {
        dots: []
      },
      realFps: null,
      sk: null,
      timers: {
        resize: null
      }
    }
  },

  mounted () {
    console.log('mounted P5: ', P5)

    new P5(sk => {
      this.sk = sk
      this.sk.setup = this.setup.bind(this)
      this.sk.draw = this.draw.bind(this)
    }, 'body')

    // new P5(this.init.bind(this), this.$el)
  },

  watch: {
    settings: {
      handler () {
        console.log('settings change')
        this.debounceResize()
      },
      deep: true
    }
  },

  methods: {
    setup () {
      this.setupGrid(this.sk.windowWidth, this.sk.windowHeight)

      window.addEventListener('resize', this.debounceResize)

      this.sk.createCanvas(this.sk.windowWidth, this.sk.windowHeight)
      this.sk.frameRate(this.settings.framerate)
      this.sk.background(this.settings.bg.r, this.settings.bg.g, this.settings.bg.b, 255)
      this.sk.noStroke()
    },

    resize () {
      console.log('resize')
      this.sk.frameRate(this.settings.framerate)
      this.sk.resizeCanvas(this.sk.windowWidth, this.sk.windowHeight)
      this.setupGrid(this.sk.windowWidth, this.sk.windowHeight)
    },

    debounceResize () {
      window.clearTimeout(this.timers.resize)
      this.timers.resize = window.setTimeout(this.resize, 100)
    },

    draw () {
      // draw a line between the previous
      // and the current mouse position
      // sk.line(sk.pmouseX, sk.pmouseY, sk.mouseX, sk.mouseY)
      this.realFps = Math.round(this.sk.frameRate())
      this.sk.background(this.settings.bg.r, this.settings.bg.g, this.settings.bg.b, this.settings.bg.a)

      this.fill()
    },

    setupGrid (width, height) {
      console.log('setupGrid: ', width, height)
      this.data.dots = []
      this.settings.grid.width = width
      this.settings.grid.height = height

      this.settings.grid.cols = Math.floor(width / this.settings.grid.cellWidth)
      this.settings.grid.rows = Math.floor(height / this.settings.grid.cellHeight)

      for (let i = 0; i < this.settings.grid.cols; i++) {
        for (let j = 0; j < this.settings.grid.cols; j++) {
          const size = Math.floor(Math.random() * (this.settings.dot.maxSize - this.settings.dot.minSize + 1)) + this.settings.dot.minSize

          this.data.dots.push({
            x: i * this.settings.grid.cellWidth,
            y: j * this.settings.grid.cellHeight,
            w: size,
            h: size,
            grow: size > this.settings.dot.minSize && size < this.settings.dot.maxSize ? Math.random() < 0.5 : !size
          })
        }
      }
    },

    fill () {
      // k.noStroke()
      this.sk.fill(this.settings.dot.r, this.settings.dot.g, this.settings.dot.b, this.settings.dot.a)
      this.sk.ellipseMode(this.sk.RADIUS)
      this.data.dots.forEach((dot) => {
        const hasSize = !!dot.w
        dot.grow = dot.w > this.settings.dot.minSize && dot.w < this.settings.dot.maxSize ? dot.grow : !hasSize

        const mod = dot.grow ? 1 : -1
        dot.w = dot.w + mod
        dot.h = dot.h + mod
        this.sk.ellipse(dot.x, dot.y, dot.w, dot.h)
      })
    }
  }
}
</script>

